@import "../../style/variables";
@import "../../style/fun";
.service{
    width: 100%;
    max-width: 30rem;
    margin: 2rem 1rem ;
    background: rgba(0, 0, 0, 0.1);
    @include flex(center,center,column,nowrap);
    padding: 1rem;
    padding-top: 3.5rem;
    box-shadow: 0.6rem 0.6rem 1rem 0.2rem black, -0.5rem -0.5rem 1rem 0.2rem #00000047;
    border: none;

    border-radius: 4rem 0rem 4rem 0rem;
    
    &:hover{
        box-shadow: -0.6rem -0.6rem 1rem 0.2rem black, 0.5rem 0.5rem 1rem 0.2rem #00000047;

      
        transform: scale(0.98);
    }
    p{
        
        max-width: 100%;
        text-align: center;
        margin: 1rem 0;
        word-break: break-word;
        font-size: 1.3rem;
    }
    h3{
        font-size: 1.4rem;
        text-align: center;
        margin: 1rem 0;
        margin-top: 5rem;
    }
    .service_logo_container{
        border: 0.1rem solid $light_blue;
        position: relative;
        width: 6rem;
        height: 6rem;
        transform: rotate(45deg);
        @include flex(center,center,column,nowrap);
        span{
            @include flex(center,center,column,nowrap);
            transform: rotate(-45deg);
      
            // position: absolute;
            // top: 50%;
            // left: 50%;
            // transform: translate(-50%,-50%);
            font-size: 3rem;
            color: $light_blue;
        }
    }
}