
#loading_preloader_heading, #loading_preloader_progress {
    font-size: 2.3rem;
    margin: 2rem 0rem;
    color: #bdbdbd;
    font-family: monospace;
    letter-spacing: 0.2rem;
    text-shadow: 0.1rem 0.1rem 1.5rem #053e37;
    background: linear-gradient(45deg, #50914b, #246274);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent;
    -moz-text-fill-color: transparent;
  }
  
  .preloader_main_container {
    padding: 1rem;
    background-color: #0c0c0c;
    width: 100%;
    z-index: 100;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .loading_bar_container {
    width: 100%;
    border: 0.1rem solid rgba(1, 171, 217, 0.1098039216);
    height: 0.8rem;
    border-radius: 1rem;
    background: rgba(123, 211, 225, 0.1803921569);
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .loading_bar_container .loading_bar {
    width: 0%;
    height: 100%;
    transition: 1s;
    background: linear-gradient(45deg, #2f5c2c, #044e5e);
    border-radius: 1rem;
  }
  
  #loading_preloader_progress {
    align-self: self-start;
    text-shadow: 0.1rem 0.1rem 1.5rem #053e37;
  }
  
  .inner_preloader_container {
    width: 90%;
    max-width: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .bbody::-webkit-scrollbar {
    display: none !important;
  }/*# sourceMappingURL=preloader_style_sheet.css.map */